/*--------------------------------------------------------------
. Add breakpoints and colors to g[side]-[breakpoint]-[color] classes
--------------------------------------------------------------*/
/**
 * This will allow us to use gradient-[breakpoint]-[side]-[color] classes with bootstrap breackpoints and UDS colors:
 * .g[side]-[breakpoint]-[color]
 * .gt-lg-gold
 * .gb-md-maroon
 * .gl-sm-gray-7
 * .gr-xs-white
 * .ov-gray-5
 * .ov-xl-gray-1
 * And so on ...
 *
 */
.gt-gold,
.gr-gold,
.gb-gold,
.gl-gold,
.ov-gold {
  position: relative;
  z-index: 0;
}
.gt-gold:before,
.gr-gold:before,
.gb-gold:before,
.gl-gold:before,
.ov-gold:before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.gt-gold:before {
  background-image: linear-gradient(0deg, rgba(255, 198, 39, 0) 0%, rgba(255, 198, 39, 0.79) 100%);
}

.gr-gold:before {
  background-image: linear-gradient(90deg, rgba(255, 198, 39, 0) 0%, rgba(255, 198, 39, 0.79) 100%);
}

.gb-gold:before {
  background-image: linear-gradient(180deg, rgba(255, 198, 39, 0) 0%, rgba(255, 198, 39, 0.79) 100%);
}

.gl-gold:before {
  background-image: linear-gradient(270deg, rgba(255, 198, 39, 0) 0%, rgba(255, 198, 39, 0.79) 100%);
}

.ov-gold:before {
  background-color: rgba(255, 198, 39, 0.79);
}

