/*--------------------------------------------------------------
1. Single.php, story hero, no background image.
--------------------------------------------------------------*/
.single .uds-story-hero.no-image {
  grid-template-rows: calc(8rem - 4.5rem) 4.5rem auto;
}
.single .uds-story-hero.no-image .hero, .single .uds-story-hero.no-image picture {
  height: 100%;
  width: 100%;
}

/*--------------------------------------------------------------
2. Content width, mobile
--------------------------------------------------------------*/
.single article {
  padding: 1rem;
}

.single article > * {
  margin-left: auto;
  margin-right: auto;
}

