/*--------------------------------------------------------------
# Overlay Card Block

1. Mobile
2. Desktop lg
3. Desktop xl
--------------------------------------------------------------*/

.home-overlay-card {
	position: relative;
	img {
		width: 100%;
		transition: 0.3s;
		object-fit: cover;
		height: 100%;
	}
	img,
	picture {
		object-fit: cover;
		object-position: center;
		height: inherit;
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
	&.with-desc {
		img,
		picture {
		}
		min-height: 330px;
	}
	&:not(.with-desc) {
		min-height: 540px;
	}
	.card-header,
	.card-body,
	.overlay-card-button {
		z-index: 3;
		color: #fff;
	}
	.card-header {
		background-color: unset;
		margin-top: auto;
		padding-bottom: 15px;
		// width: 50%;
		overflow-wrap: normal;
		padding-bottom: 24px;

		flex-grow: initial !important;
	}
	.card-body {
		flex-grow: initial;
		padding-bottom: 10px;
	}
	.overlay-card-button {
		margin-top: 0;
		padding: 32px 32px 16px 32px;
		&:not(.with-desc) {
			//width: 45%;
			//position: absolute;
			//right: 0;
			/*bottom: 0;*/
			//margin-left: auto;
			padding-top: 0;
			.btn {
				//float: right;
			}
		}
	}
	img.hover-image {
	}
	picture:nth-of-type(1),
	img:nth-of-type(1) {
		z-index: 1;
	}
	picture:nth-of-type(2),
	img:nth-of-type(2) {
		z-index: 0;
	}
	&:not(.with-desc) {
		picture:nth-of-type(2),
		img:nth-of-type(2) {
			position: absolute;
			top: 0;
			left: 0;
		}
		/*.card-content{
 		position: absolute;
 		left: 0;
 		bottom:0;
	}*/
	}
	.card-content {
		/*	display: table;
		//position: absolute;
