		width: 100%;
		padding: 32px;
		z-index: 3;
	h3{
		color: #fff;
    //font-weight: 600;
    //font-size: 1.5rem;
		width: 50%;
		display: table-cell;
		margin-bottom: 0;
		vertical-align: middle;
   &.with-desc{
    width: 100%;
		display: block;
		margin-bottom: 16px;
		margin-top: 125px;
	}*/
	}
	p {
		//color:#fff;
