	.overlay-card-button {
		/*    float: right;
				padding-bottom: 0;
				padding-right: 0;
				padding-left: 5px;
				&.with-desc{
		     float:none;
padding-left: 0px;
		 	 }
	}*/
	}

	&:after {
		content: "";
		/*background: -webkit-gradient(linear,left top,left bottom,from(#19191900),to(#191919c9));
		background: -webkit-linear-gradient(top,#19191900 60%,#191919c9 100%);
		background: -moz-linear-gradient(top,#19191900 60%,#191919c9 100%);
		background: -o-linear-gradient(top,#19191900 60%,#191919c9 100%);*/
		background: linear-gradient(180deg, #19191900 12%, #191919c9 100%);
		height: 100%;
		width: 100%;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 2;
	}

	&:hover {
		img.card-image {
			opacity: 0;
		}
	}
	&.card {
		padding: 0;
	}
}

